.search {
  position: relative;
  left: 20%;
  bottom: 0%;
  background-color: rgba(255, 255, 255, 0.897);
  border: none;
  border-radius: 10px;
  width: 250px;
  height: 40px;
  display: top;
  align-items: flex-start;
  margin-left: -25px;
}

.btn {
  background: transparent;
  border: transparent;
  color: black;
  margin-top: -10%;
}

.input {
  margin-top: 10px;
  margin-left: 10px;
  width: 200px;
  height: 20px;
  border-color: rgb(180, 51, 51);
  background: transparent;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
}

.icon {
  height: 20px;
  width: 20px;
  color: rgb(153, 24, 24);
  margin-top: 60px;
  margin-left: 180px;
}
