.danger {
  color: red;
  font-size: 15px;
}

.inputDanger {
  border-color: red;
  list-style: none;
}

.background {
  background-image: url("D:/PI-Dogs-main/client/src/img/Dogs.jpg");
  background-size: 80%;
  padding: 90px;
}

.label {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  font-weight: bold;
  list-style: none;
  margin-top: 5px;
}

.select {
  width: 100px;
  height: 50px;
  display: inline-block;
  justify-content: center;
  border-color: solid black;
  border-radius: 20px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: white;
  list-style: none;
}

.button {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 10px;
  color: solid black;
  border-color: solid black;
  background-color: rgb(246, 255, 126);
  border-radius: 60px;
  color: solid black;
  font-size: 15px;
  font-weight: 800;
  width: 70px;
  height: 70px;
}

.input {
  font-family: Arial, Helvetica, sans-serif;
  color: solid black;
  border-color: solid black;
  background-color: white;
  border-radius: 10px;
  color: solid black;
  font-size: 15px;
  font-weight: 800;
  width: 150px;
  height: 50px;
  margin-top: 10px;
}
